*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  font-family: var(--font-sans);
  font-size: var(--text-base);
  line-height: 1.6;
  background: hsl(var(--color-neutral-950));
  color: hsl(var(--color-neutral-100));
  min-height: 100vh;
  overflow-x: hidden;
}
::selection {
  background: hsl(var(--color-primary-500) / 0.4);
  color: hsl(var(--color-neutral-50));
}
:focus-visible {
  outline: 2px solid hsl(var(--color-accent-500));
  outline-offset: 2px;
  border-radius: var(--radius-sm);
}
h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-display);
  font-weight: 600;
  line-height: 1.2;
  letter-spacing: -0.02em;
  color: hsl(var(--color-neutral-50));
}
h1 {
  font-size: var(--text-5xl);
}
h2 {
  font-size: var(--text-4xl);
}
h3 {
  font-size: var(--text-3xl);
}
h4 {
  font-size: var(--text-2xl);
}
h5 {
  font-size: var(--text-xl);
}
h6 {
  font-size: var(--text-lg);
}
p {
  max-width: 70ch;
  color: hsl(var(--color-neutral-300));
}
a {
  color: hsl(var(--color-primary-400));
  text-decoration: none;
  transition: color var(--duration-fast) var(--ease-out-expo);
}
a:hover {
  color: hsl(var(--color-accent-400));
}
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
::-webkit-scrollbar-track {
  background: hsl(var(--color-neutral-900));
}
::-webkit-scrollbar-thumb {
  background: hsl(var(--color-primary-600));
  border-radius: var(--radius-full);
  border: 2px solid hsl(var(--color-neutral-900));
}
::-webkit-scrollbar-thumb:hover {
  background: hsl(var(--color-primary-500));
}
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}
@keyframes pulse-glow {
  0%, 100% {
    box-shadow: 0 0 20px hsl(var(--color-primary-500) / 0.3);
  }
  50% {
    box-shadow: 0 0 40px hsl(var(--color-primary-500) / 0.5), 
                0 0 60px hsl(var(--color-primary-500) / 0.3);
  }
}
@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}
@keyframes gradient-shift {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes wave {
  0% {
    transform: scaleY(1);
  }
  50% {
    transform: scaleY(0.5);
  }
  100% {
    transform: scaleY(1);
  }
}
@keyframes rotate-slow {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.animate-float {
  animation: float 4s ease-in-out infinite;
}
.animate-pulse-glow {
  animation: pulse-glow 3s ease-in-out infinite;
}
.animate-shimmer {
  background: linear-gradient(
    90deg,
    hsl(var(--color-neutral-800)) 0%,
    hsl(var(--color-neutral-700)) 50%,
    hsl(var(--color-neutral-800)) 100%
  );
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite;
}
.animate-gradient {
  background-size: 200% 200%;
  animation: gradient-shift 8s ease infinite;
}
.animate-rotate-slow {
  animation: rotate-slow 20s linear infinite;
}
.glass {
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur));
  -webkit-backdrop-filter: blur(var(--glass-blur));
  border: 1px solid var(--glass-border);
}
.glass-card {
  background: linear-gradient(
    135deg,
    hsl(var(--color-neutral-800) / 0.5) 0%,
    hsl(var(--color-neutral-900) / 0.3) 100%
  );
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid hsl(var(--color-neutral-700) / 0.3);
  border-radius: var(--radius-xl);
}
.glow-primary {
  box-shadow: var(--shadow-glow);
}
.glow-accent {
  box-shadow: var(--shadow-glow-accent);
}
.text-gradient {
  background: var(--gradient-hero);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-2);
  padding: var(--space-3) var(--space-6);
  font-family: var(--font-sans);
  font-weight: 500;
  font-size: var(--text-sm);
  line-height: 1;
  border-radius: var(--radius-lg);
  cursor: pointer;
  transition: all var(--duration-normal) var(--ease-spring);
  border: none;
  position: relative;
  overflow: hidden;
}
.btn::before {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, transparent 0%, hsl(0 0% 100% / 0.1) 50%, transparent 100%);
  opacity: 0;
  transition: opacity var(--duration-fast) var(--ease-out-expo);
}
.btn:hover::before {
  opacity: 1;
}
.btn-primary {
  background: var(--gradient-hero);
  background-size: 200% 200%;
  color: white;
  box-shadow: var(--shadow-md), 0 0 20px hsl(var(--color-primary-500) / 0.3);
}
.btn-primary:hover {
  transform: translateY(-2px) scale(1.02);
  box-shadow: var(--shadow-lg), 0 0 40px hsl(var(--color-primary-500) / 0.4);
  animation: gradient-shift 3s ease infinite;
}
.btn-primary:active {
  transform: translateY(0) scale(0.98);
}
.btn-secondary {
  background: hsl(var(--color-neutral-800));
  color: hsl(var(--color-neutral-100));
  border: 1px solid hsl(var(--color-neutral-700));
}
.btn-secondary:hover {
  background: hsl(var(--color-neutral-700));
  border-color: hsl(var(--color-primary-500) / 0.5);
  transform: translateY(-2px);
}
.btn-ghost {
  background: transparent;
  color: hsl(var(--color-neutral-300));
}
.btn-ghost:hover {
  background: hsl(var(--color-neutral-800) / 0.5);
  color: hsl(var(--color-neutral-100));
}
.input {
  width: 100%;
  padding: var(--space-3) var(--space-4);
  background: hsl(var(--color-neutral-900));
  border: 1px solid hsl(var(--color-neutral-700));
  border-radius: var(--radius-lg);
  color: hsl(var(--color-neutral-100));
  font-size: var(--text-base);
  transition: all var(--duration-normal) var(--ease-out-expo);
}
.input:focus {
  outline: none;
  border-color: hsl(var(--color-primary-500));
  box-shadow: 0 0 0 4px hsl(var(--color-primary-500) / 0.15);
}
.input::placeholder {
  color: hsl(var(--color-neutral-500));
}
.container {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 var(--space-6);
}
@media (max-width: 768px) {
  .container {
    padding: 0 var(--space-4);
  }
}
.card {
  background: hsl(var(--color-neutral-900));
  border: 1px solid hsl(var(--color-neutral-800));
  border-radius: var(--radius-xl);
  overflow: hidden;
  transition: all var(--duration-normal) var(--ease-spring);
}
.card:hover {
  transform: translateY(-4px) scale(1.01);
  border-color: hsl(var(--color-primary-500) / 0.3);
  box-shadow: var(--shadow-xl), 0 0 30px hsl(var(--color-primary-500) / 0.1);
}
.badge {
  display: inline-flex;
  align-items: center;
  padding: var(--space-1) var(--space-3);
  font-size: var(--text-xs);
  font-weight: 500;
  border-radius: var(--radius-full);
  background: hsl(var(--color-primary-500) / 0.2);
  color: hsl(var(--color-primary-300));
  border: 1px solid hsl(var(--color-primary-500) / 0.3);
}
.waveform {
  display: flex;
  align-items: center;
  gap: 2px;
  height: 24px;
}
.waveform-bar {
  width: 3px;
  background: hsl(var(--color-primary-400));
  border-radius: var(--radius-full);
  animation: wave 0.8s ease-in-out infinite;
}
.waveform-bar:nth-child(1) {
  animation-delay: 0s;
  height: 50%;
}
.waveform-bar:nth-child(2) {
  animation-delay: 0.1s;
  height: 75%;
}
.waveform-bar:nth-child(3) {
  animation-delay: 0.2s;
  height: 100%;
}
.waveform-bar:nth-child(4) {
  animation-delay: 0.3s;
  height: 60%;
}
.waveform-bar:nth-child(5) {
  animation-delay: 0.4s;
  height: 80%;
}
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
  html {
    scroll-behavior: auto;
  }
}
.bg-grid {
  background-image: linear-gradient(hsl(var(--color-neutral-800) / 0.3) 1px, transparent 1px),
    linear-gradient(90deg, hsl(var(--color-neutral-800) / 0.3) 1px, transparent 1px);
  background-size: 40px 40px;
}
.bg-noise {
  position: relative;
}
.bg-noise::before {
  content: '';
  position: absolute;
  inset: 0;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 256 256' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.9' numOctaves='4' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E");
  opacity: 0.03;
  pointer-events: none;
